import pageHeader from '../../../components/pageHeader.vue'
export default {
	data(){
		return{
			swiperList:[],//轮播图
			categoryList:[],//分类
			promotion:[],  //活动
			goodsList: [], // 商品列表大数组
			filterby: "all", // 默认选择的tab
			page: 1,
			size: 6,
			loadingText: "正在加载....",
			currentSwiper:0
		}
	},
	components:{
		pageHeader
	},
	onLoad(){
		this.initData()
		this.loadData()
	},
	methods:{
		handleGoods(goods){
			//页面跳转 商品详情  把当前点击商品数据传过去
			uni.navigateTo({
				url:'../../goods/goods?goodsInfo='+JSON.stringify(goods)
			})
		},
		handlePromotion(item) {
			uni.showToast({
				title: item.title,
				icon: 'none'
			})
		},
		handleCategory(item) {
			// 分类跳转
			 //console.log(item.name);
			uni.navigateTo({
				url:"../../goods/goodsList?name="+item.name 
			})
		},
		initData(){
				this.request({
					  url:"https://www.wudongming.top/uni/mall_list",
					  success:(res)=>{
						  this.swiperList = res.data.swiperList
						  this.categoryList = res.data.categoryList
						  this.promotion = res.data.promotion
					  }
				  })
		},
		swiperChange(event){
			//console.log(event.detail.current)
			this.currentSwiper = event.detail.current
		},
		loadData() {
				this.request({
					url:`https://www.wudongming.top/uni/type_list/0/${this.page}/${this.size}`,
					success: ((res) => {
						/* this.goodsList = res.data
						console.log(this.goodsList) */
						if(res.data.length > 0){
							res.data.forEach(item => {
								this.goodsList.push(item);
							})
						}else{
							this.loadingText = "到底了";
						}
					})
				})
		},
		onReachBottom(){ //上拉触发
				this.page++;
			   this.loadData()
		}
	}
}
